import React, { Component } from 'react';
import {Chart} from 'react-google-charts';
import {fetchDailyData} from './../../API/index';
import PieChart from './GooglePieChart';
import styles from './../GoogleCharts/googleCharts.module.css';
class LineChart extends Component{
    constructor(){
        super();
        this.state={
            data:[]
        }
    }
    async componentDidMount(){
     this.setState({data:await fetchDailyData()})
    }
    render(){
        console.log(this.state.data);
        //const dataRetrieved=this.props.data;
        //const keys=Object.keys(data);
        const data = [];
        data.push(['date', 'Confirmed', 'Deaths']);
        this.state.data.map(details => {
        const detailsArray = [];
        detailsArray.push(details.date);
        detailsArray.push(details.confirmed);
        detailsArray.push(details.deaths);
        data.push(detailsArray);
         });
        const lineChart= <Chart
        width={'500px'}
        height={'300px'}
        chartType="LineChart"
        loader={<div>Loading Chart</div>}
        data={data}
        options={{
          hAxis: {
            title: 'Date',
          },
          vAxis: {
            title: 'Corona Cases',
          },
          series: {
            1: { curveType: 'function' },
          },
        }}
        rootProps={{ 'data-testid': '2' }}
      />
      return <div className={this.props.country? styles.pieChartDimension :styles.lineChartDimension}>
      {this.props.country? <PieChart data={this.props.data} country={this.props.country}/> : lineChart}
      </div>
    }
}
export default LineChart;