import React, { Component } from 'react';
import {Chart} from 'react-google-charts';
//import styles from './../Charts/GoogleCharts/googleCharts.module.css';
class PieChart extends Component{
    render(){
      console.log(this.props.data);
      const retrievedData=this.props.data;         
        return <Chart 
          width={'500px'}
          height={'300px'}
          chartType="PieChart"
          loader={<div>Loading Chart</div>}
          data={[
            ['Cases', 'Numbers'],
            ['Active', (retrievedData.confirmed.value - retrievedData.deaths.value - retrievedData.recovered.value)],
            ['Deaths', retrievedData.deaths.value],
            ['Recovered', retrievedData.recovered.value]   
          ]}
          options={{
            title: `Current status in ${this.props.country}`,
            is3D: true,
          }}
          rootProps={{ 'data-testid': '2' }}
        />
    }
}
export default PieChart;